import { Controller } from '@ali/4ever-cangjie';
import { QuickInsertPluginConfig, InsertItem } from '../types';
import insertQuickInsert from '../commands/insert';
import removeQuickInsert from '../commands/remove';
import isSelectionInQuickInsert from '../queries/isSelectionInQuickInsert';

export default function createOnCangjieInput(
  config: QuickInsertPluginConfig,
  insertMap: Record<string, InsertItem>,
) {
  const { triggers } = config;
  return function onCangjieInput(
    event: CustomEvent,
    controller: Controller,
    next: () => void,
  ) {
    const plugin = triggers[event.detail.data!];

    if (!plugin || !insertMap[plugin]) {
      return next();
    }

    event.preventDefault();

    if (isSelectionInQuickInsert(controller)) {
      const { decorations } = controller.value;
      // 若此时仍尝试呼出相同插入，则重新呼出
      const quickInsertDecoration = decorations.find(
        ({ mark }) => mark.type === 'quick-insert',
      );
      if (quickInsertDecoration!.mark.data.plugin === plugin) {
        return controller
          .command(removeQuickInsert)
          .command(insertQuickInsert, { plugin });
      }
      return next();
    }
    return controller.command(insertQuickInsert, { plugin });
  };
}
